import {defineStore} from 'pinia';

export const useVideoData = defineStore({
    id: "videoData",
    state: () => {
        return {
            videoData: [{
                VideoUrl: "https://filems.xiudodo.com/library/transcode/videos/a07a06a9-bed7-53a2-1a44-19871eceb0e6/watermark/hd.mp4?auth_key=1665598800-0-0-d068c178dbcb82b1ef956f9aea17b3f2",
                Cover: "风景1",
                Title: "风景1",
                CollStr: "风景",
            },
                {
                    VideoUrl: "https://filems.xiudodo.com/library/transcode/videos/1b701edd-9470-2c19-62ec-b49cf284e9ff/watermark/hd.mp4?auth_key=1665599400-0-0-ba4374280bc16f09893fe0a8510c6e61",
                    Cover: "风景2",
                    Title: "风景2",
                    CollStr: "风景",
                }, {
                    VideoUrl: "https://filems.xiudodo.com/library/transcode/videos/dffb15d6-7d38-7b28-373f-4c7d4574bed7/watermark/hd.mp4?auth_key=1665599400-0-0-992d54b0fe1b0c699ffd03c99ad1b984",
                    Cover: "风景3",
                    Title: "风景3",
                    CollStr: "风景",
                }],
        }
    },
    actions: {
        addVideoData(val) {
            this.videoData.push(...val);
        },
        setVideoData(val) {
            this.videoData = val;
        }
    },
    persist: {
        enabled: false,
        strategies: [{
            key: 'videoData',
            storage: localStorage,
        }]
    }
})